














const defaultMoneyIntl = new Intl.NumberFormat("zh-Hans-CN", {
    currency: "CNY"
});

const moneyIntl = new Intl.NumberFormat("zh-Hans-CN", {
    currency: "CNY",
    style: "currency",
    minimumFractionDigits: 2
});

const enMoneyIntl = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD"
});

const ruMoneyIntl = new Intl.NumberFormat("ru-RU", {
    style: "currency",
    currency: "RUB"
});

const msMoneyIntl = new Intl.NumberFormat("ms-MY", {
    style: "currency",
    currency: "MYR"
});

const getTextByLocale = (locale: string | undefined, paramsText: number) => {
    let text = paramsText;
    if (typeof text === "string") {
        text = Number(text);
    }
    if (locale === "en_US") {
        // english
        return enMoneyIntl.format(text);
    }
    // russian
    if (locale === "ru_RU") {
        return ruMoneyIntl.format(text);
    }
    // malay
    if (locale === "ms_MY") {
        return msMoneyIntl.format(text);
    }
    if (locale === undefined) {
        return defaultMoneyIntl.format(text);
    }
    return moneyIntl.format(text);
};
import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

@Component({
    name: "MyMoney"
})
export default class MyMoney extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;
    /**金额符号 */
    @Prop({ default: "￥" })
    symbol!: string;
    /**占位符 */
    @Prop({ default: "请输入" })
    placeholder!: string;

    get initValue() {
        return getTextByLocale("zh-CN", this.currentValue);
    }
}
